<template>
  <v-container ref="boxIds" app>
    <div class="info_data">
      <div class="guides_box" v-if="name_sign === 'faq_guides'">
        <p class="guides_title">{{ guidesTitle }}</p>
        <div class="rich_text" v-html="infoData"></div>
        <div v-for="(item, index) in guidesList" :key="index" class="guides_item">
          <div class="item_content">
            <div class="content_icon">
              <icon-font
                :name="getGuidesIcon(index)"
                :dark="variables.help_info_guides_content_icon_color"
                :size="
                  isApp
                    ? variables.help_info_guides_content_icon_app_size
                    : variables.help_info_guides_content_icon_pc_size
                "
              />
            </div>
            <div class="content_right">
              <div class="content_title">{{ item.title || '' }}</div>
              <div class="content_text">{{ item.paging_name || '' }}</div>
            </div>
          </div>
          <div class="item_bottom">
            <span @click="handlerMore(item)">{{ $t('UI_more') }}</span>
          </div>
        </div>
      </div>
      <div v-else-if="this.name_sign != 'faq'">
        <img width="100%" :src="infoUrl" v-if="infoUrl" />
        <div class="rich_text" v-html="infoData"></div>
      </div>
      <div v-else class="column_flex">
        <span class="faq_title">{{ $t('UI_FAQ') }}</span>
        <div v-if="faqList.list_six.length > 0" class="six v-btn-wrap">
          <v-btn
            class="t_card_bg faq_btn"
            :height="variables.help_info_faq_six_btn_height"
            depressed
            v-for="item in faqList.list_six"
            :key="item.id"
            @click="onHelpInfo(item)"
          >
            {{ item.title }}
          </v-btn>
        </div>
        <div v-if="faqList.list_more.length > 0" class="more">
          <v-btn
            class="t_card_bg faq_btn"
            width="100%"
            depressed
            :height="variables.help_info_faq_more_btn_height"
            v-for="item in faqList.list_more"
            :key="item.id"
            @click="onHelpInfo(item)"
          >
            {{ item.title }}
          </v-btn>
        </div>
      </div>
    </div>
  </v-container>
</template>

<script>
import { mapState } from 'vuex'
import { getInfoCache } from '@/api/modules/config.js'
import ContainerMixin from '@/components/containerMixin'
import variables from '@style/views/menu.help_info.modules.scss'
import { jump } from '@/utils/linkMap'
export default {
  name: 'helpInfo',
  mixins: [ContainerMixin],
  data() {
    return {
      variables,
      name_sign: '', // 请求标识 原前端定义的标识 faq_button 替换为接口下发标识：faq
      infoData: '', // 信息数据
      infoUrl: '' // 图片信息
    }
  },
  computed: {
    ...mapState('base', ['user', 'languageType', 'isApp']),
    ...mapState('source', ['furyInfo']),
    ...mapState('layout', ['containerBox']),
    // FAQ 的数据列表
    faqList() {
      const { faq_list = [] } = this.furyInfo

      let list_six = [] // 前 6 个
      let list_more = [] // 6 个后的全部
      if (faq_list && Array.isArray(faq_list)) {
        list_six = faq_list.slice(0, 6)
        list_more = faq_list.slice(6)
      }

      return {
        list_six,
        list_more
      }
    },
    // 指南标题
    guidesTitle() {
      if (this.name_sign === 'faq_guides') {
        const { index_list = [] } = this.furyInfo
        const target = index_list.find(item => item.name === this.name_sign) || {}
        return target.title || ''
      } else {
        return ''
      }
    },
    // 指南列表
    guidesList() {
      if (this.name_sign === 'faq_guides') {
        const { guide_faq_list = [] } = this.furyInfo
        return Array.isArray(guide_faq_list) ? guide_faq_list : []
      } else {
        return []
      }
    }
  },
  watch: {
    // 监听传过来的参数
    '$route.query': {
      handler({ name = '' }) {
        this.name_sign = name
        this.infoData = ''
        this.infoUrl = ''

        // name == 'faq_button' 的时候是自己添加的类别，另做处理
        // faq_button => faq 标识提换
        if (!name || name == 'faq') return
        this.getInfo()
      },
      deep: true,
      immediate: true
    },
    // 监听国际化切换
    languageType(n) {
      this.infoData = ''
      this.infoUrl = ''

      // name == 'faq_button' 的时候是自己添加的类别， 不重新请求
      // faq_button => faq 标识提换
      if (this.name_sign == 'faq') return

      this.getInfo()
    }
  },
  methods: {
    // 获取 帮助 说明
    async getInfo() {
      const params = {
        token: this.user.token,
        language: this.languageType,
        name: this.name_sign,
        type: 1
      }

      this.$pop.loading().show()
      const { code, data = {} } = await getInfoCache(params).catch(e => e)
      this.$pop.loading().hide()
      if (code != 0) return

      const info = data.value || ''
      this.infoData = info
      this.infoUrl = data.img_url || ''
    },
    // 跳转服务信息页面
    onHelpInfo(item = {}) {
      const { name = '' } = item
      if (!name) return

      this.$router.push({ name: 'helpInfo', query: { name } })
    },
    handlerMore(item) {
      if (item.link_type == 1 && !item.inside_data) {
        this.$router.push({ name: 'helpInfo', query: { name: item.name } })
        return
      }
      if (item.link_type == 2 && item.link) {
        item.outside = item.link
      }
      jump(item, this, false)
    },
    getGuidesIcon(index) {
      const icons = [
        'icon-Slots',
        'icon-qiuyi',
        'icon-a-GuiasCopa',
        'icon-a-GuiasEuro',
        'icon-shouji',
        'icon-jiangbei',
        'icon-F'
      ]
      const i = index % icons.length
      return icons[i]
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/views/menu.help_info.modules.scss';
.info_data {
  min-height: $help_info_min_height;
  .faq_title {
    font-size: $help_info_faq_title_font_size;
    font-weight: $help_info_faq_title_font_weight;
    margin: $help_info_faq_title_font_margin;
  }
  .guides_box {
    .guides_title {
      font-size: $help_info_guides_title_pc_size;
      font-weight: $help_info_guides_title_pc_weight;
      color: $help_info_guides_title_pc_color;
      margin: $help_info_guides_title_pc_margin;
    }
    .guides_item {
      border-radius: $help_info_guides_item_pc_radius;
      background: $help_info_guides_item_pc_background;
      margin: $help_info_guides_item_pc_margin;
      &:last-child {
        margin: 0;
      }
      .item_content {
        position: relative;
        min-height: $help_info_guides_item_content_pc_min_height;
        padding: $help_info_guides_item_content_pc_padding;
        .content_icon {
          position: absolute;
          top: $help_info_guides_content_icon_pc_top;
          left: $help_info_guides_content_icon_pc_left;
          @include setBox(row, center, center);
          width: $help_info_guides_content_icon_pc_width;
          height: $help_info_guides_content_icon_pc_height;
          border-radius: $help_info_guides_content_icon_pc_radius;
          background: $help_info_guides_content_icon_pc_background;
        }
        .content_right {
          margin: $help_info_guides_content_right_pc_margin;
          .content_title {
            font-weight: $help_info_guides_content_title_pc_weight;
            font-size: $help_info_guides_content_title_pc_size;
            color: $help_info_guides_content_title_pc_color;
            margin: $help_info_guides_content_title_pc_margin;
          }
          .content_text {
            font-weight: $help_info_guides_content_text_pc_weight;
            font-size: $help_info_guides_content_text_pc_size;
            color: $help_info_guides_content_text_pc_color;
            line-height: $help_info_guides_content_text_pc_line_height;
            white-space: pre-wrap;
            word-break: break-all;
          }
        }
      }
      .item_bottom {
        height: $help_info_guides_item_bottom_pc_height;
        line-height: $help_info_guides_item_bottom_pc_line;
        padding: $help_info_guides_item_bottom_pc_padding;
        border-top: $help_info_guides_item_bottom_pc_border_top;
        font-weight: $help_info_guides_item_bottom_pc_weight;
        font-size: $help_info_guides_item_bottom_pc_size;
        color: $help_info_guides_item_bottom_pc_color;
        text-align: right;
        cursor: pointer;
      }
    }
  }
  ::v-deep table {
    width: $help_info_guides_table_width;
    border: 1px solid $help_info_guides_table_border_color;
    tr {
      border-bottom: 1px solid $help_info_guides_table_border_color;
      td {
        border-right: 1px solid $help_info_guides_table_border_color;
      }
    }
  }
}
.faq_btn {
  font-size: $help_info_faq_btn_font_size;
  margin: $help_info_faq_btn_margin;
  color: $help_info_faq_btn_color;
  ::v-deep .v-btn__content {
    line-height: $help_info_faq_btn_line_height !important;
  }
}

.six {
  .faq_btn {
    width: $help_info_faq_six_btn_width;
    &:nth-child(even) {
      margin-right: 0 !important;
    }
  }
}
.six,
.more {
  width: 100% !important;

  ::v-deep .v-btn__content {
    width: 100% !important;
  }
}
.rich_text {
  color: $help_info_guides_rich_pc_color;
  white-space: pre-wrap;
  word-break: break-all;
}

@include breakpoint-only-xs() {
  .info_data {
    .guides_box {
      padding: $help_info_guides_box_padding;
      .guides_title {
        font-size: $help_info_guides_title_app_size;
      }
      .guides_item {
        border-radius: $help_info_guides_item_app_radius;
        margin: $help_info_guides_item_app_margin;
        .item_content {
          min-height: $help_info_guides_item_content_app_min_height;
          padding: $help_info_guides_item_content_app_padding;
          .content_icon {
            top: $help_info_guides_content_icon_app_top;
            left: $help_info_guides_content_icon_app_left;
            width: $help_info_guides_content_icon_app_width;
            height: $help_info_guides_content_icon_app_height;
            border-radius: $help_info_guides_content_icon_app_radius;
          }
          .content_right {
            margin: $help_info_guides_content_right_app_margin;
            .content_title {
              font-size: $help_info_guides_content_title_app_size;
              margin: $help_info_guides_content_title_app_margin;
            }
            .content_text {
              font-size: $help_info_guides_content_text_app_size;
              line-height: $help_info_guides_content_text_app_line_height;
            }
          }
        }
        .item_bottom {
          height: $help_info_guides_item_bottom_app_height;
          line-height: $help_info_guides_item_bottom_app_line;
          font-size: $help_info_guides_item_bottom_app_size;
        }
      }
    }
  }
}
</style>
